@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,600;1,400&display=swap");
/* font-family: 'Montserrat', sans-serif; */
/* 
######################
    GLOBAL STYLES
######################
*/
* {
  padding: 0;
  margin: 0;
  font-family: "Montserrat", sans-serif;
  box-sizing: border-box;
}
input[type="number"], input[type="text"], textarea {
  box-shadow: inset 0 0 5px 0 black;
}

input[type="number"][disabled="true"], input[type="text"][disabled="true"], textarea[disabled="true"] {
  box-shadow: none
}

body {
  background: lightgray;
}

ul {
  list-style: none;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1.3rem;
}

input {
  border: none;
  background: transparent;
  font-size: inherit;
}

textarea {
  resize: none !important;
  background: transparent;
  border: none;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  appearance: none;
}

/* 
######################
    GRID STYLES
######################
*/
/* Display grid */
.grid {
  display: grid;
  gap: 1rem;
  width: 100%;
  grid-auto-rows: min-content;
}
.grid.fr-3 {
  grid-template-columns: repeat(3, 1fr);
}
.grid.fr-2 {
  grid-template-columns: repeat(2, 1fr);
}
.grid.wrap {
  grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
}

/* Grid items */
.c-span-2 {
  grid-column: span 2;
}

.c-span-3 {
  grid-column: span 3;
}

/* CONTAINER */
.container {
  width: 90%;
  margin: auto;
  padding: 1rem;
}

/* 
######################
    FLEX STYLES
######################
*/
.flex {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  width: 100%;
}
.flex.column {
  flex-direction: column;
}
.flex.align-start {
  align-items: flex-start;
}
.flex.justify-space-between {
  justify-content: space-between;
}

/* 
######################
    INPUT STYLES
######################
*/
.input-big {
  width: 100%;
  height: 100%;
  font-size: 2rem;
}

.cb-big {
  width: 2rem;
  height: 2rem;
}

.xp {
  padding: 0 .5rem;
}

/* 
######################
    BOX STYLES
######################
*/
.box-white {
  background: white;
  width: 100%;
  padding: 0.5rem;
  color: black;
  height: 4rem;
}

.box-dark {
  background: #333;
  color: #eee;
  width: 100%;
  padding: 0.5rem;
  position: relative;
}

.circle-white {
  background: white;
  border-radius: 100%;
  width: 5rem;
  height: 5rem;
  text-align: center;
}

/* 
######################
    ABILITY STYLES
######################
*/
.ability-scores {
  display: grid;
  gap: 1rem;
  width: 100%;
  grid-auto-rows: min-content;
  grid-column: span 2;
  grid-template-columns: repeat(3, 1fr);
}

.ability {
  width: 6rem;
  height: 6rem;
  box-shadow: 4px 4px 4px 2px #333;
  margin-top: 1rem;
  padding-bottom: 1.5rem;
}

.ability-label {
  color: black;
}

.ability-box {
  height: 9rem;
  margin-bottom: 2rem;
}

.ability-condition {
  position: absolute;
  right: 1rem;
  bottom: 2rem;
}

.ability-description {
  text-align: center;
  grid-column: span 3;
}

.ability-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: black;
  background: white;
}

.ability-input {
  width: 3.5rem;
  font-size: 2rem;
  height: 100%;
  text-align: center;
}

.ability-mod {
  font-size: 1.5rem;
}

/* 
######################
    ARMOR HP DAMAGE STYLES
######################
*/
.input-right {
  background: white;
  border-radius: 100%;
  width: 5rem;
  height: 5rem;
  text-align: center;
  position: absolute;
  top: -1rem;
  right: 5rem;
}

.undertext-left {
  color: #000000;
  position: absolute;
  right: 12rem;
  top: 3rem;
}

.undertext-right {
  color: #000000;
  position: absolute;
  right: 1.5rem;
  top: 3rem;
}

.max-hp-class {
  display: inline-block;
  width: 1.5rem;
  text-align: center;
}

.armor-icon {
  position: absolute;
  width: 100px;
  right: 5rem;
  top: -1rem;
}

.armor-input {
  width: 5rem;
  height: 5.5rem;
  text-align: center;
  position: absolute;
  top: -1rem;
  right: 5.6rem;
  background: transparent;
  box-shadow: none !important;
  font-size: 2rem;
  text-decoration: underline;
}

.hp-icon {
  position: absolute;
  width: 120px;
  right: 4.5rem;
  top: -1rem;
}

.hp-total {
  box-shadow: none !important;
  position: absolute;
  right: 9rem;
  top: 0;
  width: 2rem;
  text-align: center;
  font-size: 1.5rem;
  color: black;
}

.hp-current {
  box-shadow: none !important;
  text-decoration: underline;
  color: black;
  font-size: 1.5rem;
  position: absolute;
  right: 5.5rem;
  width: 2rem;
  top: 0rem;
  text-align: center;
}

.damage {
  position: absolute;
  right: 4.5rem;
  top: -2rem;
}

.damage-text {
  position: absolute;
  right: 4.7rem;
  top: 0;
  color: black;
  font-size: 2.5rem;
  font-weight: bold;
  -webkit-text-stroke: 1px white;
  text-align: center;
  width: 120px;
}

.mb-5 {
  margin-bottom: 5rem;
}

/* 
######################
    INVENTORY STYLES
######################
*/
.coin {
  position: absolute;
  left: 1rem;
  top: -1.5rem;
}

.coin-title {
  margin-left: 9rem;
}

.coin-input {
  position: absolute;
  background: none;
  width: 3rem;
  font-size: 1.5rem;
  left: 3.5rem;
  top: 0.5rem;
  text-align: center;
  box-shadow: none !important;
  text-decoration: underline;
}

.input-left {
  background: white;
  border-radius: 100%;
  width: 5rem;
  height: 5rem;
  text-align: center;
  position: absolute;
  top: -1rem;
}

.coin-item {
  margin-bottom: 4rem;
}

.gear-bag {
  position: absolute;
  right: 4rem;
  top: -3rem;
}

.gear-head {
  margin-bottom: 3rem;
}

.load-current {
  position: absolute;
  right: 5rem;
  color: black;
  top: .5rem;
  font-size: 1.5rem;
  text-align: center;
  width: 2rem;
}

.load-total {
  position: absolute;
  color: white;
  right: 7.3rem;
  top: .5rem;
  font-size: 1.5rem;
  text-align: center;
  width: 2rem;
}

.undertext-gear-left {
  color: #000000;
  position: absolute;
  right: 11rem;
  top: 3rem;
}

.undertext-gear-right {
  color: #000000;
  position: absolute;
  right: 0rem;
  top: 3rem;
}

.max-load {
  width: 1.6rem;
  text-align: center;
}

.inventory {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.item {
  display: flex;
  gap: 0.5rem;
  width: 100%;
}
.item__desc {
  width: 79%;
  line-height: 1.7;
  font-size: 1rem;
}
.item__uses {
  width: 10%;
  text-align: center;
  height: 2rem;
  font-size: 1.2rem;
}
.item__weight {
  width: 10%;
  text-align: center;
  height: 2rem;
  font-size: 1.2rem;
}

.desc {
  font-size: 0.8rem;
  display: flex;
  gap: 0.5rem;
  width: 100%;
  margin-bottom: 0.5rem;
}
.desc__desc {
  width: 78%;
}
.desc__uses {
  width: 9%;
  text-align: center;
}
.desc__weight {
  width: 11%;
  text-align: center;
}

/* 
######################
    TEXT AND LIST ITEM STYLES
######################
*/
.preface {
  padding: .5rem 0;
  font-style: italic;
}

.upper {
  text-transform: uppercase;
}

.li {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.5rem;
  padding: .5rem 0;
  width: 100%;
}
.li__top {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  width: 100%;
}
.li__checkbox {
  width: 2rem;
  height: 2rem;
}
.li__title {
  text-transform: capitalize;
  width: 100%;
  font-size: 1.3rem;
  font-weight: bold;
}
.li__text {
  width: 100%;
  line-height: 1.7;
  font-size: 1rem;
}
.li__text--i {
  font-style: italic;
}
.li__text--p {
  padding-left: 2.5rem;
}

.pb-2 {
  padding-bottom: 2rem;
}

/* 
######################
    BUTTON STYLES
######################
*/
.add {
  width: 100%;
  height: 3rem;
  font-size: 1.4rem;
}

.delete {
  width: 2rem;
  height: 2rem;
  font-size: 1.2rem;
  background: darkred;
  color: white;
}

.hide {
  display: none;
}

@media (max-width: 1250px) {
  .ability-description {
    grid-column: span 2;
  }
  .ability-scores {
    grid-template-columns: auto;
  }
}

@media (max-width: 1200px) {
  .grid {
    display: flex;
    flex-direction: column;
  }
}

@media (max-width: 650px) {
	.ability-description {
		grid-column: span 1;
	}
}